import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { HTTP } from '@ionic-native/http';

import { MyApp } from './app.component';

//tab切换
import { TabsPage } from '../pages/tabs/tabs';
//首页
import { PageIndexPage} from "../pages/page-index/page-index";
//病历列表页面
import { ListIndexPage } from '../pages/list-index/list-index'
//病历录入模块列表页面
import { InputIndexPage} from "../pages/input-index/input-index";
//病历录入录入页面
import { InputListPage} from "../pages/input-list/input-list";


//表单组件
import { FormComponent } from "../components/form/form"

@NgModule({
  declarations: [
    MyApp,
    ListIndexPage,
    TabsPage,
    PageIndexPage,
    InputIndexPage,
    FormComponent,
    InputListPage
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp,{
      iconMode: 'ios',//安卓icon强制使用ios的icon以及样式
      mode: 'ios',//样式强制使用ios样式
    })
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    ListIndexPage,
    TabsPage,
    PageIndexPage,
    InputIndexPage,
    FormComponent,
    InputListPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    HTTP,
    {provide: ErrorHandler, useClass: IonicErrorHandler}
  ]
})
export class AppModule {}



